import React, {useEffect, useState} from 'react';

import {
    Avatar,
    Button,
    Cell,
    Div,
    Group,
    Header,
    Panel,
    PanelHeader,
    PanelHeaderBack,
    Placeholder,
    Separator
} from '@vkontakte/vkui';
import {Icon56CakeCircleFillRaspberryPink, Icon56Fire, Icon56FireOutline} from '@vkontakte/icons';

const Friend = ({id, go, fetchedUser, friend, meShacking, coord, speed, setMeShacking}) => {
    const [friendShacking, setFriendShacking] = useState(false)
    const [friendThere, setFriendThere] = useState(true)

    useEffect(() => {
        const getFriendInfoTimer = setInterval(() => {
            const requestOptions = {
                method: 'GET',
                headers: {'Content-Type': 'application/json'},
                body: JSON.stringify({id: friend.id})
            };

            fetch('http://104.248.245.109/get_handshake', requestOptions)
                .then(response => response.json())
            // new Promise((resolve, reject) => {
            //     setTimeout(() => {
            //         resolve({friend_id: Math.random() < 0.8 ? 0 : 1, shake: Math.random() < 0.5});
            //     }, 300)
            // })
                .then(data => {
                    setFriendThere(() => {
                        return data.friend_id === fetchedUser.id
                    })
                    setFriendShacking(() => {
                        return data.shake === 1 || data.shake === "1"
                    })
                })
        }, 500);

        const sendMyInfoTimer = setInterval(() => {
            setMeShacking((prev) => {
                if (fetchedUser === null || friend === null)
                    return prev
                const requestOptions = {
                    method: 'POST',
                    headers: {'Content-Type': 'application/json'},
                    body: JSON.stringify({id: fetchedUser.id, friend_id: friend.id, shake: prev ? 1 : 0})
                };
                console.log("body: " + JSON.stringify({id: fetchedUser.id, friend_id: friend.id, shake: prev}))
                fetch('http://104.248.245.109/set_handshake', requestOptions)
                return prev
            })
        }, 500);

        return () => {
            console.log("unmount")
            clearTimeout(getFriendInfoTimer);
            clearTimeout(sendMyInfoTimer)

            if (fetchedUser !== null) {
                const requestOptions = {
                    method: 'PUT',
                    headers: {'Content-Type': 'application/json'},
                    body: JSON.stringify({id: fetchedUser.id, friend_id: null, shake: false})
                };
                fetch('https://jsonplaceholder.typicode.com/posts/1', requestOptions)
            }

        }
    }, []);

    return <Panel id={id}>
        <PanelHeader
            left={<PanelHeaderBack onClick={go} data-to={'Home'}/>}
        >Тет-а-тет</PanelHeader>
        {/*        <Cell>x: {coord.x}</Cell>
        <Cell>y: {coord.y}</Cell>
        <Cell>z: {coord.z}</Cell>
        <Cell>speed: {speed}</Cell>*/}
        <Group header={<Header mode="secondary">Вы</Header>}>
            <Div style={{display: 'flex'}}>
                <Button size="l" stretched mode="commerce" style={{marginRight: 8}}>Вы в тет-а-тет</Button>
                {meShacking ?
                    <Button size="l" stretched mode="commerce">Вы трясете</Button>
                    : <Button size="l" stretched mode="secondary">Вы не трясете</Button>
                }
            </Div>
        </Group>
        <Group header={<Header
            mode="secondary">{(friend !== null ? friend.first_name : "...") + " " + (friend !== null ? friend.last_name : "")}</Header>}>
            <Div style={{display: 'flex'}}>
                {friendThere ?
                    <Button size="l" stretched mode="commerce" style={{marginRight: 8}}>В тет-а-тет</Button>
                    : <Button size="l" stretched mode="secondary" style={{marginRight: 8}}>Не в тет-а-тет</Button>
                }
                {friendShacking ?
                    <Button size="l" stretched mode="commerce">Трясет</Button>
                    : <Button size="l" stretched mode="secondary">Не трясет</Button>
                }
            </Div>
        </Group>
        <Separator/>
        {meShacking && friendThere && friendShacking ?
            <Div style={{
                margin: 0,
                display: 'flex',
                flexDirection: 'row',
                alignItems: 'center',
                justifyContent: 'center',
                textAlign: 'center',
                padding: 32,
            }}>
                <Avatar><Icon56CakeCircleFillRaspberryPink/></Avatar>

                <Cell>Вы трясете одновременно!</Cell>

                <Avatar><Icon56CakeCircleFillRaspberryPink/></Avatar>
            </Div>
            : <Placeholder
                icon={meShacking ? <Icon56Fire fill={"DC143C"}/> : <Icon56FireOutline fill={"DC143C"}/>}
                header="Трясьти нужно сильно!"
            >
            </Placeholder>}

    </Panel>
}

export default Friend;
